C,yCAAyC;AAAA;AAG1C;QACC,wDAAwD;AAAA;AAGzD;QACC,yCAAyC;AAAA;AAAA;AAAA;AC3wB3C;ADgZF;MAqYI,WAAW;MACX,aAAa;MAEb,OAAO;MACP,MAAM;MAEN,qBAAqB;AAAA;AA3YzB;QA8YK,eAAe;AAAA;AA9YpB;MCxcC,sBAAsB;MD61BnB,kBAAkB;MAClB,SAAS;MACT,OAAO;MAEP,WAAW;AAAA;AACX","file":"SweetModal.vue?vue&type=style&index=0&lang=scss&","sourcesContent":["//\n// Icons + Animation originally by SweetAlert, t4t5.\n// https://github.com/t4t5/sweetalert\n//\n\n// Success Icon\n@keyframes animateSuccessTip {\n\t0%\t\t{ width: 0;\t\tleft: 1px;\t\ttop: 19px; }\n\t54%\t\t{ width: 0;\t\tleft: 1px;\t\ttop: 19px; }\n\t70%\t\t{ width: 50px;\tleft: -8px;\t\ttop: 37px; }\n\t84%\t\t{ width: 17px;\tleft: 21px;\t\ttop: 48px; }\n\t100%\t{ width: 25px;\tleft: 14px;\t\ttop: 45px; }\n}\n\n@keyframes animateSuccessLong {\n\t0%\t\t{ width: 0;\t\tright: 46px;\ttop: 54px; }\n\t65%\t\t{ width: 0;\t\tright: 46px;\ttop: 54px; }\n\t84%\t\t{ width: 55px;\tright: 0px;\t\ttop: 35px; }\n\t100%\t{ width: 47px;\tright: 8px;\t\ttop: 38px; }\n}\n\n@keyframes rotatePlaceholder {\n\t0%\t\t{ transform: rotate(-45deg); }\n\t5%\t\t{ transform: rotate(-45deg); }\n\t12%   \t{ transform: rotate(-405deg); }\n\t100%\t{ transform: rotate(-405deg); }\n}\n\n.animateSuccessTip {\n\tanimation: animateSuccessTip 0.75s;\n}\n\n.animateSuccessLong {\n\tanimation: animateSuccessLong 0.75s;\n}\n\n.sweet-modal-icon.sweet-modal-success.animate::after {\n\tanimation: rotatePlaceholder 4.25s ease-in;\n}\n\n/* Error Icon */\n@keyframes animateErrorIcon {\n\t0%\t\t{ transform: rotateX(100deg);\topacity: 0; }\n\t100%\t{ transform: rotateX(0deg);\topacity: 1; }\n}\n\n.animateErrorIcon {\n\tanimation: animateErrorIcon 0.5s;\n}\n\n@keyframes animateXMark {\n\t0%    { transform: scale(0.4);\t margin-top: 26px;\topacity: 0; }\n\t50%   { transform: scale(0.4);\t margin-top: 26px;\topacity: 0; }\n\t80%   { transform: scale(1.15);\t margin-top: -6px;\t\t\t\t}\n\t100%  { transform: scale(1);\t margin-top: 0;\t\topacity: 1; }\n}\n\n.animateXMark {\n\tanimation: animateXMark 0.5s;\n}\n\n@keyframes pulseWarning {\n\t0%\t\t{ border-color: #F8D486; }\n\t100%\t{ border-color: #F8BB86; }\n}\n\n.pulseWarning {\n\tanimation: pulseWarning 0.75s infinite alternate;\n}\n\n@keyframes pulseWarningIns {\n\t0%\t\t{ background-color: #F8D486; }\n\t100%\t{ background-color: #F8BB86; }\n}\n\n.pulseWarningIns {\n\tanimation: pulseWarningIns 0.75s infinite alternate;\n}\n\n@keyframes rotate-loading {\n\t0%\t\t{ transform: rotate(0deg);   }\n\t100%\t{ transform: rotate(360deg); }\n}\n","@import './colors';\n@import './mixins';\n\n.sweet-modal-icon {\n\tposition: relative;\n\n\twidth: 80px;\n\theight: 80px;\n\n\tborder: 4px solid gray;\n\tborder-radius: 50%;\n\n\tmargin: auto;\n\tpadding: 0;\n\n\tbox-sizing: content-box;\n\n\t&.sweet-modal-error {\n\t\tborder-color: color(red);\n\n\t\t.sweet-modal-x-mark {\n\t\t\tposition: relative;\n\t\t\tdisplay: block;\n\t\t}\n\n\t\t.sweet-modal-line {\n\t\t\tdisplay: block;\n\t\t\tposition: absolute;\n\t\t\ttop: 37px;\n\t\t\theight: 5px;\n\t\t\twidth: 47px;\n\n\t\t\tbackground-color: color(red);\n\t\t\tborder-radius: 2px;\n\n\t\t\t&.sweet-modal-left {\n\t\t\t\ttransform: rotate(45deg);\n\t\t\t\tleft: 17px;\n\t\t\t}\n\n\t\t\t&.sweet-modal-right {\n\t\t\t\ttransform: rotate(-45deg);\n\t\t\t\tright: 16px;\n\t\t\t}\n\t\t}\n\t}\n\n\t&.sweet-modal-warning {\n\t\tborder-color: color(orange);\n\n\t\t.sweet-modal-body { // Exclamation mark body\n\t\t\tposition: absolute;\n\t\t\twidth: 5px;\n\t\t\theight: 47px;\n\t\t\tleft: 50%;\n\t\t\ttop: 10px;\n\n\t\t\tmargin-left: -2px;\n\n\t\t\tborder-radius: 2px;\n\t\t\tbackground-color: color(orange);\n\t\t}\n\t\t.sweet-modal-dot { // Exclamation mark dot\n\t\t\tposition: absolute;\n\t\t\tleft: 50%;\n\t\t\tbottom: 10px;\n\t\t\twidth: 7px;\n\t\t\theight: 7px;\n\n\t\t\tmargin-left: -3px;\n\n\t\t\tborder-radius: 50%;\n\t\t\tbackground-color: color(orange);\n\t\t}\n\t}\n\n\t&.sweet-modal-info {\n\t\tborder-color: color(blue);\n\n\t\t&::before { // i-letter body\n\t\t\tcontent: '';\n\t\t\tposition